import { useState } from "react";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl, pdfCdnUrl } from "../../../Services/UrlConfig/BaseUrl";
import style from "../../StoreManager/StoreManager.module.css";
import { useTranslation } from "react-i18next";
import { useFileUpload } from "../../../../hooks/authentication-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CreateButton from "../../../Ui/Button/CreateButton";
import { usePayrollStaffUploadForm16 } from "../../../Hooks/ApiHooks/PayrollManager/payroll-manager-api";
const PayrollForm16Card = ({ staff, pid, fYear }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [fileUpload] = useFileUpload();
  const [payrollStaffUploadForm16] = usePayrollStaffUploadForm16();

  const onUploadFile = (e) => {
    if (e.target.files?.length) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey && pid && fYear && staff?._id) {
            payrollStaffUploadForm16({
              sid: staff?._id,
              bodyParameter: {
                pid: pid,
                key: res.data?.imageKey,
                annual: fYear,
              },
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  const onDownload = (e) => {
    e.preventDefault();
    if (staff?.form_16?.key_a) {
      const newWindow = window.open(
        `${pdfCdnUrl}/${staff?.form_16?.key_a}`,
        "_blank",
        "noopener,noreferrer"
      );
      if (newWindow) newWindow.opener = null;
    }
  };
  return (
    <>
      <label htmlFor="form_16" className={style.smc_card}>
        <div
          className={style.smc_card_contatiner}
          style={{
            justifyContent: "space-between",
            width: "100%",
          }}
        >
          <div className={style.smc_card_contatiner}>
            <img
              src={
                staff?.staffProfilePhoto
                  ? `${imageShowUrl}/${staff?.staffProfilePhoto}`
                  : `${assestsFeedUrl}/feed-user-avatar.svg`
              }
              loading="lazy"
              alt="avatar"
              style={{
                height: "2.3rem",
                width: "2.3rem",
                borderRadius: "50%",
              }}
            />

            <div className={style.smc_card_contatiner_inner}>
              <h6>{`${staff?.staffFirstName ?? ""} ${
                staff?.staffMiddleName ?? ""
              } ${staff?.staffLastName ?? ""}`}</h6>
              <p>
                {t("index_number_1")} {staff?.staffROLLNO ?? ""}
              </p>
            </div>
          </div>
          {staff?.form_16?.key_a ? (
            <CreateButton
              label="download_form_16"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onDownload}
            />
          ) : (
            <CreateButton
              label="upload_form_16"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={() => {}}
            />
          )}
        </div>
      </label>
      <input
        type={"file"}
        id="form_16"
        onChange={onUploadFile}
        style={{
          display: "none",
        }}
      />
      {disabled && <QvipleLoader />}
    </>
  );
};
export default PayrollForm16Card;
